#app {
  height: 100%;
  width: 100%;
  overflow: hidden;
}

#app .left {
  text-align: center;
}

#app .left .left__Favicon, #app .left .left__Favicon img {
  display: inline-block;
}

#app .left .menu-item {
  text-align: left;
  position: relative;
}

#app .left .menu-item .menu-tag {
  padding: 0 33rem;
}

#app .left .menu-item .menu-box {
  width: 180rem;
  z-index: 100;
  background-color: #fff;
  border-radius: 8rem;
  padding: 8rem 10rem;
  position: absolute;
  top: 6rem;
  right: -170rem;
  box-shadow: 1px 3px 10px #0006;
}

#app .left .menu-item .menu-box .li {
  width: 100%;
  align-items: center;
  padding: 0 16px;
  line-height: 32rem;
  display: flex;
}

#app .left .menu-item .menu-box .li .mt-icon {
  margin-right: 3rem;
  font-size: 18rem;
}

#app .left .menu-item .menu-box .li-tl {
  color: orange;
  font-weight: bold;
}

#app .form-box {
  width: 100%;
  height: 100%;
  z-index: 1000;
  background-color: #0000004d;
  position: fixed;
  top: 0;
  left: 0;
}

#app .form-box .form-panel {
  height: 100%;
  width: 360rem;
  text-align: left;
  background-color: #fff;
}

#app .form-box .form-panel .form-title {
  padding: 20px 20px 0;
  font-size: 40rem;
  font-weight: bold;
}

#app .form-box .form-panel .form-item {
  padding: 32px 32px 0;
  display: block;
}

#app .form-box .form-panel .form-item label {
  margin-bottom: 8rem;
  font-size: 16rem;
  font-weight: bold;
  display: block;
  color: #000 !important;
}

#app .form-box .form-panel .form-item input {
  width: 100%;
  height: 48rem;
  border: 1px solid #aaa;
  border-radius: 3px;
  padding: 0 10px;
  font-size: 16rem;
  line-height: 48rem;
  display: block;
}

#app .form-box .form-panel .form-btns {
  text-align: center;
  padding: 40px 0 32px;
}

#app .form-box .form-panel .form-btns button {
  font-size: 18rem;
}

#app .content__Reading__c .content__Reading__c__box {
  color: #fff;
  padding: 0 8rem;
}

.view-footer span {
  font-size: 64px !important;
}

body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}

* {
  box-sizing: border-box;
}

.box {
  width: 100%;
  height: 100vh;
  box-sizing: border-box;
  background-color: #e5e5e5;
  padding: 34rem 42rem 57rem 0;
  display: flex;
  overflow: hidden;
}

.left {
  width: 114rem;
  box-sizing: border-box;
  min-height: 950rem;
  background: #fff;
  border-radius: 20rem;
