@media screen and (max-width: 558px) {
  .content__one {
    height: 500rem;
    flex: none;
    margin-bottom: 40rem;
    margin-right: 0;
    display: flex;
    overflow: auto;
  }

  .content {
    flex-direction: column;
    flex: 1;
    margin: 34rem 0 18rem 16rem;
    display: flex;
  }

  .content__Reading {
    width: 100%;
    box-sizing: border-box;
    height: auto;
    background: #e1f2fc;
    border-radius: 20rem;
    margin-right: 12rem;
    padding: 22rem 35rem;
    overflow: auto;
    box-shadow: 0 8rem 9rem #0000001a;
  }

  .content__Reading__c {
    height: 142rem;
    width: 90%;
    box-sizing: border-box;
    background: #ffffffd9;
    border-radius: 16rem;
    margin: 10rem auto 0;
    padding: 20rem;
  }

  .content__Reading__c__h {
    box-sizing: border-box;
    border-bottom: 3rem solid #000;
    align-items: center;
    padding: 0 5rem;
    display: flex;
  }

  .content__Reading__c__h__text {
    color: #545454;
    font-family: Inter;
    font-size: 4rem;
    font-style: normal;
    font-weight: 600;
    line-height: 10rem;
  }

  .content__toDo {
    width: 100%;
    box-sizing: border-box;
    height: auto;
    background: #e1f2fc;
    border-radius: 20rem;
    padding: 22rem 8rem;
    overflow: auto;
    box-shadow: 0 8rem 9rem #0000001a;
  }

  .content__two__h {
    margin-bottom: 30rem;
  }

  .content__two {
    width: 100%;
    height: 800rem;
    flex: 0;
    margin-bottom: 40rem;
  }

  .content__twos__box {
    flex-wrap: wrap;
    align-items: flex-start;
    display: flex;
  }

  .content__two__box {
    width: 43%;
    min-height: 250rem;
    border-radius: 16rem;
    margin: 10rem 30rem;
    padding: 25rem 28rem;
  }

  .content__two__boxs-item {
    margin-top: 30rem;
  }

  .content__two__boxs-items {
    margin-right: -10rem;
    transform: scale(.9);
  }

  .content__two__box__stop {
    width: 50rem;
    height: 50rem;
  }

  .content__three {
    height: auto;
    flex: 1;
  }

  .footer__display__center__progressbar {
    display: none;
  }

  .footer__display__center__cons {
    height: 100%;
  }

  .footer__display__center__cons img {
    height: 60rem;
    width: auto;
  }

  .footer__display__info {
    margin-top: 0;
    transform: scale(.5);
  }

  .footer__console {
    border-radius: 50rem;
  }

  .footer__display__right {
    align-items: center;
    margin: 0;
    display: flex;
  }

  .footer__display {
    border-radius: 50rem;
  }
}

